:root {
  --background: #0d0d10;
  --background-rgba1: #0d0d10;
  --background-rgba0: #0d0d1099;
  --foreground: #c2c2c3;
  --cursor: #c2c2c3;
  --color0: #0d0d10;
  --color1: #494040;
  --color2: #5c514f;
  --color3: #9c1b16;
  --color4: #d2472a;
  --color5: #996b60;
  --color6: #d2936c;
  --color7: #c2c2c3;
  --color8: #58586b;
  --color9: #494040;
  --color10: #5c514f;
  --color11: #9c1b16;
  --color12: #d2472a;
  --color13: #996b60;
  --color14: #d2936c;
  --color15: #c2c2c3;
  --radius: 15px;
}

html {
  font-family: sans-serif;
}

body, main {
  background-color: var(--background);
}

.hide {
  display: none !important;
}

.header {
  background-color: var(--color15);
  color: var(--background);
  border-radius: var(--radius);
  justify-content: space-between;
  width: 100%;
  height: 5%;
  display: flex;
}

.title {
  margin: auto 6px;
  display: inline;
}

h2 {
  color: var(--foreground);
  text-align: center;
  padding-top: 4%;
  font-size: 3rem;
}

h3 {
  color: var(--foreground);
}

#loader {
  border: .3rem solid var(--background);
  border-top: .3rem solid var(--color4);
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  animation: 2s linear infinite spin;
  display: inline-block;
}

a:hover {
  color: var(--color4);
}

a {
  color: var(--color14);
}

#dropdown-content {
  border: 5px solid var(--foreground);
  border-radius: var(--radius);
  background-color: var(--foreground);
  width: max-content;
  max-height: 85vh;
  position: absolute;
  top: 185%;
  right: 50%;
  transform: translate(50%);
}

.selected-page {
  color: var(--color3) !important;
}

.osu-username {
  color: var(--foreground);
  text-align: center;
  width: max-content;
  height: max-content;
  display: inline-block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.osu-username input {
  border-radius: var(--radius);
  background: var(--foreground);
  color: var(--background);
  border: 2px solid var(--foreground);
  margin: 0 auto;
  font-size: 1rem;
  display: block;
}

.osu-username button {
  border: 1px solid var(--foreground);
  color: var(--background);
  background: var(--foreground);
  border-radius: var(--radius);
  margin: 3% auto;
  font-size: 1rem;
  display: inline;
}

.osu-username button:hover {
  background: var(--color4);
  border-color: var(--color4);
}

.osu-username input:focus {
  border: none;
  border: 2px solid var(--foreground);
  outline: none;
}

#myTable_wrapper {
  min-width: 95%;
  max-width: 95%;
  margin: 0 auto;
}

#message {
  color: var(--color3);
  margin: 0 auto;
}

.rank-image {
  height: 2em;
}

#myTable {
  color: var(--foreground);
  border-collapse: separate;
  border-spacing: 0 1em;
  text-align: center;
}

thead {
  color: var(--foreground);
}

.table-head th {
  background: var(--background);
  border-radius: var(--radius);
}

.table-controls {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

div.dt-search {
  text-align: center;
  width: max-content;
  margin: 0 auto;
  display: inline;
}

.dt-search label {
  color: var(--foreground);
  margin: 0 auto;
}

div.dt-container .dt-search input {
  margin-left: 1%;
  background-color: var(--foreground) !important;
  border-radius: var(--radius) !important;
}

.toggle-fail {
  width: max-content;
  margin: auto;
  display: inline;
}

.toggle-fail label {
  color: var(--foreground);
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
}

.toggle-fail label input {
  opacity: 0;
  cursor: pointer;
  width: 0;
  height: 0;
  position: absolute;
}

.checkmark {
  background-color: var(--foreground);
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
  margin-left: 1em;
  position: absolute;
}

.toggle-fail label:hover input ~ .checkmark {
  background-color: var(--color4);
}

.toggle-fail label input:checked ~ .checkmark {
  background-color: var(--color3);
}

.checkmark:after {
  content: "";
  display: none;
  position: absolute;
}

.toggle-fail label input:checked ~ .checkmark:after {
  display: block;
}

.toggle-fail .checkmark:after {
  border: 3px solid #fff;
  border-width: 0 3px 3px 0;
  width: 5px;
  height: 10px;
  top: 5px;
  left: 8px;
  transform: rotate(45deg);
}

div#myTable_info.dt-info {
  color: var(--foreground);
}

.grade {
  text-align: center;
  flex-direction: row-reverse;
  justify-content: center;
  margin: auto 0;
}

.fail-percent {
  margin: 0;
  padding-left: 4%;
}

.rank-image {
  width: 3em;
  margin: auto;
}

.song-name a {
  color: var(--foreground);
  text-decoration: none;
}

.song-name a:hover {
  color: var(--color4);
}

tbody#scores-table tr {
  height: 2rem;
  font-size: 18px;
}

thead tr.table-head {
  display: hidden;
}

table.dataTable thead {
  text-align: center !important;
}

div.dt-scroll-headInner {
  background-color: #0000;
}

div#loader.osu-track-loader {
  margin-top: 5%;
}

div.stats {
  background-color: var(--foreground);
  border-radius: var(--radius);
  justify-content: space-around;
  padding: .1rem;
  display: flex;
}

div.stats stat {
  margin-right: 3rem;
  display: inline-block;
}

div.stats img {
  filter: invert(6%) sepia(1%) saturate(5941%) hue-rotate(202deg) brightness(90%) contrast(101%);
  margin-right: 3rem;
  display: inline-block;
}

.pager h4 {
  width: max-content;
  height: max-content;
  color: var(--foreground);
  margin: 0;
  display: block;
}

.sessions {
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  margin: auto;
  display: flex;
}

.pager, .pagination {
  width: max-content;
  height: max-content;
  display: inline-block;
}

.pagination img, .pagination p, .pagination div {
  margin-left: .2rem;
  margin-right: .2rem;
  display: inline-block;
}

.pagination p {
  color: var(--foreground);
  text-align: center;
  width: 1.2rem;
}

.pagination p:hover {
  color: var(--color4);
  cursor: pointer;
}

.start-end-div {
  color: var(--foreground);
  width: max-content;
  height: max-content;
  display: inline-block;
}

#star-rating {
  white-space: nowrap;
}

#star-rating img, #star-rating p {
  display: inline-block;
}

.profile {
  height: 10rem;
  padding: 1rem;
  position: relative;
}

.pro-container {
  display: inline-block;
  overflow: hidden;
}

.pro-comp {
  justify-content: center;
  align-items: center;
  width: max-content;
  height: max-content;
  display: flex;
}

.pro-info {
  width: max-content;
  height: max-content;
  color: var(--foreground);
  display: inline-block;
}

.pro-image-container {
  display: inline-block;
}

.pro-image-container img {
  border-radius: 100%;
  height: 8rem;
}

.pro-ul {
  list-style: none;
}

.graph {
  width: 90%;
  height: 500px;
  margin: auto;
}

div.dt-container .dt-search input {
  background-color: var(--foreground) !important;
}
