:root {
    /* Special */
    --background: #0D0D10;
    --background-rgba1: rgba(13, 13, 16, 1);
    --background-rgba0: rgba(13, 13, 16, 0.6);
    --foreground: #c2c2c3;
    --cursor: #c2c2c3;

    /* Colors */
    --color0: #0D0D10;
    --color1: #494040;
    --color2: #5C514F;
    --color3: #9C1B16;
    --color4: #D2472A;
    --color5: #996B60;
    --color6: #D2936C;
    --color7: #c2c2c3;
    --color8: #58586b;
    --color9: #494040;
    --color10: #5C514F;
    --color11: #9C1B16;
    --color12: #D2472A;
    --color13: #996B60;
    --color14: #D2936C;
    --color15: #c2c2c3;

    /* Other Commen styles*/
    --radius: 15px;
}

html {
    font-family: sans-serif;
}

body {
    background-color: var(--background);
}

main {
    background-color: var(--background);
}

.hide {
    display: none !important;
}

.header {
    background-color: var(--color15);
    color: var(--background);
    width: 100%;
    height: 5%;
    border-radius: var(--radius);
    display: flex;
    justify-content: space-between;
}

.title {
    display: inline;
    margin: auto 6px auto;
}

h2 {
    color: var(--foreground);
    text-align: center;
    padding-top: 4%;
    font-size: 3rem;
}

h3 {
    color: var(--foreground);
}

#loader {
    display: inline-block;
    border: .3rem solid var(--background);
    border-top: .3rem solid var(--color4);
    border-radius: 50%;
    width: 1rem;
    height: 1rem;
    animation: spin 2s linear infinite;
}

a:hover {
    color: var(--color4);
}

a {
    color: var(--color14);
}

#dropdown-content {
    position: absolute;
    border: 5px solid var(--foreground);
    border-radius: var(--radius);
    background-color: var(--foreground);
    width: max-content;
    top: 185%;
    right: 50%;
    max-height: 85vh;
    transform: translate(50%);
}

.selected-page {
    color: var(--color3) !important;
}

.osu-username {
    display: inline-block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: var(--foreground);
    height: max-content;
    width: max-content;
    text-align: center;
}

.osu-username input {
    margin: 0 auto;
    display: block;
    border-radius: var(--radius);
    background: var(--foreground);
    color: var(--background);
    border: 2px solid var(--foreground);
    font-size: 1rem;
}

.osu-username button {
    display: inline;
    border: 1px solid var(--foreground);
    color: var(--background);
    background: var(--foreground);
    border-radius: var(--radius);
    font-size: 1rem;
    margin: 3% auto;
}

.osu-username button:hover {
    background: var(--color4);
    border-color: var(--color4);
}

.osu-username input:focus {
    outline: none;
    border: none;
    border: 2px solid var(--foreground);
}

#myTable_wrapper {
    max-width: 95%;
    min-width: 95%;
    margin: 0 auto;
}

#message {
    margin: 0 auto;
    color: var(--color3);
}

.rank-image {
    height: 2em;
}


#myTable {
    color: var(--foreground);
    border-collapse: separate;
    border-spacing: 0 1em;
    text-align: center;
}

thead {
    color: var(--foreground);
}

.table-head th {
    background: var(--background);
    border-radius: var(--radius);
}

.table-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

div.dt-search {
    text-align: center;
    width: max-content;
    display: inline;
    margin: 0 auto;
}

.dt-search label {
    margin: 0 auto;
    color: var(--foreground);
}

div.dt-container .dt-search input {
    background-color: var(--foreground) !important;
    border-radius: var(--radius) !important;
    margin-left: 1%;
}

.toggle-fail {
    width: max-content;
    display: inline;
    margin: auto;
}

.toggle-fail label {
    color: var(--foreground);
    cursor: pointer;
    user-select: none
}

.toggle-fail label input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    height: 1.5em;
    width: 1.5em;
    background-color: var(--foreground);
    border-radius: 50%;
    margin-left: 1em;
}

.toggle-fail label:hover input~.checkmark {
    background-color: var(--color4);
}

.toggle-fail label input:checked~.checkmark {
    background-color: var(--color3);
}

.checkmark::after {
    content: "";
    position: absolute;
    display: none;
}

.toggle-fail label input:checked~.checkmark::after {
    display: block;
}

.toggle-fail .checkmark::after {
    left: 8px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
}

div#myTable_info.dt-info {
    color: var(--foreground);
}

.grade {
    flex-direction: row-reverse;
    justify-content: center;
    text-align: center;
    margin: auto 0;
}

.fail-percent {
    padding-left: 4%;
    margin: 0;
}

.rank-image {
    margin: auto;
    width: 3em;
}

.song-name a {
    color: var(--foreground);
    text-decoration: none;
}

.song-name a:hover {
    color: var(--color4);
}

tbody#scores-table tr {
    font-size: 18px;
    height: 2rem;
}

thead tr.table-head {
    display: hidden;
}

table.dataTable thead {
    text-align: center !important;
}

div.dt-scroll-headInner {
    background-color: rgba(0, 0, 0, 0);
}

div#loader.osu-track-loader {
    margin-top: 5%;
}

div.stats {
    display: flex;
    justify-content: space-around;
    background-color: var(--foreground);
    padding: .1rem;
    border-radius: var(--radius);
}

div.stats stat {
    display: inline-block;
    margin-right: 3rem;
}

div.stats img {
    display: inline-block;
    margin-right: 3rem;
    filter: invert(6%) sepia(1%) saturate(5941%) hue-rotate(202deg) brightness(90%) contrast(101%);
}

.pager h4 {
    display: block;
    height: max-content;
    width: max-content;
    color: var(--foreground);
    margin: 0;
}

.sessions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row-reverse;
    width: 95%;
    margin: auto;
}

.pager {
    display: inline-block;
    width: max-content;
    height: max-content;
}

.pagination {
    display: inline-block;
    width: max-content;
    height: max-content;
}

.pagination img,
.pagination p,
.pagination div {
    display: inline-block;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
}

.pagination p {
    color: var(--foreground);
    width: 1.2rem;
    text-align: center;
}

.pagination p:hover {
    color: var(--color4);
    cursor: pointer;
}

.start-end-div {
    display: inline-block;
    color: var(--foreground);
    height: max-content;
    width: max-content;
}

#star-rating {
    white-space: nowrap;
}

#star-rating img,
#star-rating p {
    display: inline-block;
}

.profile {
    position: relative;
    padding: 1rem;
    height: 10rem;
}

.pro-container {
    display: inline-block;
    overflow: hidden;
}

.pro-comp {
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    height: max-content;
}

.pro-info {
    display: inline-block;
    width: max-content;
    height: max-content;
    color: var(--foreground);
}

.pro-image-container {
    display: inline-block;
}

.pro-image-container img {
    border-radius: 100%;
    height: 8rem;
}

.pro-ul {
    list-style: none;
}

.graph {
    height: 500px;
    width: 90%;
    margin: auto;
}

div.dt-container .dt-search input {
    background-color: var(--foreground) !important;
}
